body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.albumsContainer {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-row-gap: 3rem;
  row-gap: 3rem;
  margin-left: 5rem;
  margin-right: 1rem;
}

img {
  width: 15rem;
  height: 15rem;
  margin-top: 0.5rem;
}

body {
  background-color: #232f34;
  font-family: "Inter", sans-serif;
}

.album {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 16rem;
  height: 20rem;
  border-radius: 15px;
  background-color: #344955;
  color: white;
  font-weight: 600;
}

.album:hover {
  box-shadow: 0 0 40px #f9aa33;
  cursor: pointer;
}

.titleHeader, .description, .score {
  color: #f9aa33;
  margin-left: 1rem;
  margin-right: 1rem;
}

.headerAndScore {
  display: grid;
  grid-template-columns: 1.5fr 0.5fr;
}

.score {
  margin-left: 10rem;
  margin-right: 1rem;
}

@media (max-width: 900px) {
  .titleHeader {
    font-size: 1.5rem;
  }
  .description {
    font-size: 1rem;
    text-align: center;
  }
.score {
  margin-left: 3rem;
}
}

